.responsebar{
    position: absolute;
    /* bottom: 20px;
    left: 20px; */
    width: auto;
    left: 30px;
    height: 60px;
    border-radius: 5px;
    /* background-color: #202020; */
    background-color: #212121;
    z-index: 8;
    display: -webkit-flex;
    display: flex;
    box-shadow: 0px 5px 10px 0px rgba(0,0,0,1);
    /* box-shadow: 0px 0px 20px 3px rgba(255,255,255,0.05); */
    /* box-shadow: 0px 0px 10px 3px rgba(0,0,0,1); */
}
.responsebar.up{
    bottom: -100px;
    -webkit-animation: goUp 0.3s ease-in-out 0s 1 normal forwards;
            animation: goUp 0.3s ease-in-out 0s 1 normal forwards;
}
.responsebar.down{
    bottom: 100px;
    -webkit-animation: goDown 0.3s ease-in-out 0s 1 normal forwards;
            animation: goDown 0.3s ease-in-out 0s 1 normal forwards;
}
.responsebar.start{
    bottom: -100px;
}
@-webkit-keyframes goUp{
    to {
        bottom: 100px;
    }
}
@keyframes goUp{
    to {
        bottom: 100px;
    }
}
@-webkit-keyframes goDown{
    to {
        bottom: -100px;
    }
}
@keyframes goDown{
    to {
        bottom: -100px;
    }
}
.responsebar.notplaying.up{
    bottom: -100px;
    -webkit-animation: notplayingGoUp 0.3s ease-in-out 0s 1 normal forwards;
            animation: notplayingGoUp 0.3s ease-in-out 0s 1 normal forwards;
}
.responsebar.notplaying.down{
    bottom: 30px;
    -webkit-animation: goDown 0.3s ease-in-out 0s 1 normal forwards;
            animation: goDown 0.3s ease-in-out 0s 1 normal forwards;
}
.responsebar.start{
    bottom: -100px;
}
@-webkit-keyframes notplayingGoUp{
    to {
        bottom: 30px;
    }
}
@keyframes notplayingGoUp{
    to {
        bottom: 30px;
    }
}

